.app{height:100vh;display:flex;justify-content:center;align-items:center}.login{width:40%;min-height:50%;border-radius:1.3em;border:1px solid gray;display:flex;justify-content:center;align-items:center}.login.light{background:#f9f4f4}.login.dark{background:#111}.container{width:50%;display:flex;justify-content:center;align-items:center;padding:1em}.container form{width:100%}.container span{font-size:1.2em}.checkbox__container{position:relative}.checkbox__label{display:flex}.checkbox__label:before{box-sizing:border-box;content:"";border-radius:.15em;width:21px;height:21px;border:1px solid #000}input[type=checkbox]{box-sizing:border-box;position:absolute;top:0;left:0;cursor:pointer;opacity:0;width:21px;height:21px;margin:0}input[type=checkbox]:hover+label::before{background-color:#9381ef}input[type=checkbox]:checked+label::before{font-size:1.3em;content:"âœ”";transition-duration:.55s;background:linear-gradient(45deg, rgb(120, 8, 211), rgba(47, 14, 215, 0.332));box-sizing:border-box;padding-bottom:3px;display:flex;align-items:center;justify-content:center;color:#fff}.forms{display:flex;flex-direction:column}.forms__email,.forms__email:focus{display:flex;flex-direction:column}.forms__email input,.forms__email:focus input{align-self:center;height:1.5em;width:75%;margin-top:.8em;font-size:1.05em;padding:3px 8px;border-radius:.7em}.forms__email span,.forms__email:focus span{margin-left:1.55em}.forms__captcha{margin:0em 0em 1em 1.9em}.forms__captcha input{font-size:.9em;width:85%;padding:5px;border-radius:.3em}.forms__captcha img{height:75px;width:150px;margin-bottom:.5em;border:1px solid gray;border-radius:.2em}.forms__password,.forms__password:focus{display:flex;flex-direction:column;margin-top:.5em}.forms__password input,.forms__password:focus input{align-self:center;height:1.5em;width:75%;font-size:1.05em;margin-top:.8em;padding:3px 8px;border-radius:.7em}.forms__password span,.forms__password:focus span{margin-left:1.55em}.forms__rememberMe{display:flex;align-items:baseline;margin:1em 0em 1em 1.85em}.forms__rememberMe span{font-size:1.1em;margin-left:1.2em}.forms__rememberMe_wrap{display:flex;flex-direction:column}.forms__rememberMe_wrap button{align-self:center;width:81.5%;background-image:linear-gradient(to right, #6e0ebc 10%, rgba(146, 254, 247, 0.4705882353) 51%, #001aff 100%);padding:5px 10px;text-align:center;text-transform:uppercase;transition:.5s;background-size:200% auto;color:#fff;box-shadow:0 0 5px #eee;border-radius:.3em;display:block}.forms__rememberMe_wrap button span{font-size:1.2em;font-weight:600;font-style:italic;letter-spacing:.19em}.forms button:hover{cursor:pointer;background-position:right center;color:#faad08;text-decoration:none}