.layout{
    width: 100%;
    min-height: 100vh;
}
.layout.dark{
    
    background: rgba(0, 0, 0, 0.918);
    color: white;
    a{
        color: white;
    }
    button{
        background: rgb(109, 3, 230);
        color: white;
    }
}
.layout.light{
    background: rgba(255, 255, 255, 0.918);
    color: rgb(0, 0, 0);
    a{
        color: black;
    }
    button{
        background: white;
        color: black;
    }
}