.addbtn{
    margin: 1em 0.2em;
    button{
        
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 80%;
        height: 2em;
        border: none;
        background: rgba(48, 46, 46, 0) !important;
        span{
            font-size: 1.45em;
            padding-top: 0.1em;
        }
        img{
            width: 23px;
            height: 23px;
            margin-right: 2em;
        }
        img path{
            fill-rule: white;
        }
    }
}
.addbtn:hover{
    
    button{
        width: 80%;
        height: 2em;
        border: none;
        transition-duration: 0.3s;
        cursor: pointer;
        span{
            font-size: 1.45em;
            padding-top: 0.1em;
        }
        img{
            width: 23px;
            height: 23px;
            margin-right: 2em;
            transition-duration: 0.3s;
            background-color: rgba(255, 255, 255, 0.305);
            border-radius: 50%;
            transition-property: background;
        }
    }
}

